@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Roboto Condensed', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.content__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.home__container {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.login__container {
  display: -webkit-flex;
  display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: #f0f2f5;
}

.logo_container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.logo_container span {
  font-family: 'Roboto Condensed', 'Segoe UI', 'Roboto', sans-serif;
  margin: 20px 0 0 10px;
  font-size: 20px;
  font-weight: 700;
} 


.login-form {
  max-width: 400px;
  min-width: 300px;
}

.login-form .ant-form-item {
  margin: 10px;
}

.login-form .ant-form-item .ant-input-prefix {
  color: #1890ff;
}

.remember__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.not-found__container {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
}

.not-authenticated__container {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
}

.quiz-challenge__container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: auto;
  margin: 20px 20px 0 40px;
}

.view-result__container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: auto;
  margin: 20px 20px 0 40px;
}

.footer {
  text-align: center;
  width: 100%;
  padding: 16px;
}

.nav__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  box-shadow: 0px 2px 8px #F0F1F2;
}

.nav__logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.nav__app-name {
  color: black;
  margin-left: 30px;
  font-weight: 600;
  cursor: pointer;
} 

.menu__container {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
